.appointment {
  &-confirmation {
    background: #fff;
    padding: 50px 80px;
    border: 1px solid #306185;
    box-shadow: 4px 4px 10px rgba(0, 0, 50, 0.1);
    border-radius: 4px;
    width: 100%;
    line-height: 145%;
    max-width: 602px;
    text-align: center;
    &__icon {
      margin-bottom: 30px;
    }
    &__title {
      font-weight: 600;
      font-size: 38px;
      line-height: 125%;
      color: #141431;
      margin-bottom: 30px;
      @include response(900px) {
        font-size: 32px;
      }
    }
    &__caption {
      margin-bottom: 30px;
    }
    .btn {
      margin: 0 auto;
      max-width: 351px;
      width: 100%;
    }
    @include response(900px) {
      padding: 40px 20px;
    }
  }
}