
.page-wrapp {
  overflow: hidden;
}
.main-heading {
  background: #EEF5FF;
  margin-bottom: 100px;
  &__holder {
    max-width: 1410px;
    padding: 0 15px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 40px;
    @include response(900px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__thumb {
    max-width: 50%;
    flex-shrink: 0;
    @include response(900px) {
      max-width: 100%;
    }
  }
  &__inner {
    max-width: 580px;
    @include response(900px) {
      width: 100%;
    }
  }
  &__title {
    font-weight: 600;
    font-size: 72px;
    line-height: 125%;
    color: #141431;
    margin-bottom: 30px;
    span {
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-image: linear-gradient(90deg, #CC7F32 -0.48%, #FFCC8F 99.76%);
    }
    @include response(1300px) {
      font-size: 54px;
    }
    @include response(1000px) {
      font-size: 38px;
    }
  }
  &__descr {
    margin-bottom: 30px;
  }
  @include response(900px) {
    margin-bottom: 60px;
  }
}


