.blog-list {
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  margin-bottom: 100px;
  @include response(900px) {
    gap: 20px;
  }
  @include response(680px) {
    justify-content: center;
    margin-bottom: 60px;
  }
}

.blog-item {
  text-decoration: none;
  color: #5D5D70;
  border-radius: 4px;
  overflow: hidden;
  max-width: calc(33.33333% - 34px);
  width: 360px;
  &__thumb {
    overflow: hidden;
    img {
      @include transition();
    }
  }
  &__inner {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 10px 0 0;
  }
  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 155%;
    color: #141431;
    @include transition();
  }
  &__caption {
    line-height: 145%;
  }
  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #EEF5FF;
    border-radius: 4px;
    padding-left: 10px;
  }
  &__date {
    font-size: 14px;
    line-height: 16px;
    span {
      font-weight: 500;
    }
  }
  &__category {
    height: 32px;
    display: flex;
    align-items: center;
    padding: 0 5px;
    background: linear-gradient(159.61deg, #CC7F32 0%, #FFCC8F 100.06%);
    border-radius: 0 4px 4px 0;
    font-weight: 700;
    font-size: 10px;
    line-height: 155%;
    color: #fff;
    text-transform: uppercase;
  }
  &.main {
    max-width: calc(50% - 25px);
    width: 565px;
    .blog-item {
      &__inner {
        background: #EEF5FF;
        padding: 30px;
        @include response(900px) {
          padding: 20px;
        }
      }
      &__title {
        font-size: 26px;
      }
      &__info {
        background: #D0E4F2;
      }
    }
    @include response(680px) {
      max-width: 100%;
      width: 335px;
    }
  }
  &:hover {
    .blog-item__thumb img {
      transform: scale(1.1);
    }
    .blog-item__title {
      color: #CC7F32;
    }
  }
  @include response(900px) {
    max-width: calc(33.33333% - 15px);
  }
  @include response(680px) {
    max-width: 100%;
    width: 335px;
  }
}

