.results {
  padding: 100px 0;
  &-holder {
    max-width: 1180px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    gap: 100px;
    @include response(1200px) {
      padding-left: 15px;
    }
    @include response(900px) {
      flex-wrap: wrap;
      gap: 40px;
      justify-content: center;
    }
    @include response(767px) {
      justify-content: flex-start;
    }
  }
  &-inner {
    max-width: 480px;
    width: 100%;
    flex-shrink: 0;
    .btn {
      height: 49px;
      max-width: 241px;
      font-size: 16px;
    }
  }
  &__title {
    font-weight: 600;
    font-size: 48px;
    line-height: 125%;
    margin-bottom: 30px;
    color: #141431;
    span {
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-image: linear-gradient(90deg, #CC7F32 -0.48%, #FFCC8F 99.76%);
    }
    @include response(900px) {
      font-size: 32px;
    }
  }
  &__caption {
    margin-bottom: 30px;
  }
  &-slider {
    width: 970px;
    max-width: 73%;
    margin-left: -10px;
    &__item {
      margin: 0 5px;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        left: calc(50% - 100px);
        top: calc(50% - 100px);
        width: 200px;
        height: 200px;
        background-image: url("../data/img-logo.svg");
        background-size: contain;
      }
      img {
        margin: 0 auto;
        opacity: .5;
        @include transition();

      }
      &-caption {
        position: absolute;
        left: 5px;
        right: 5px;
        bottom: 0;
        height: 46px;
        background: #D0E4F2;
        border-radius: 0px 0px 4px 4px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 14px;
        color: #141431;
        opacity: 0;
        @include transition();
      }
    }
    .slick-active {
      img {
        opacity: 1;
      }
      .results-slider__item-caption {
        opacity: 1;
      }
    }
    .slick-arrow {
      z-index: 2;
      height: 64px;
      width: 34px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: #306185;
      border-radius: 4px;
      &::before {
        content: '';
        width: 22px;
        height: 22px;
        opacity: 1;
        background-image: url("../data/arrow.svg");
        background-size: contain;
      }
    }
    .slick-prev {
      left: 5px;
    }
    .slick-next {
      right: 5px;
      &::before {
        transform: scale(-1, 1);
      }
    }
    @include response-min(1700px) {
      max-width: 100%;
      margin: 0;
      .slick-prev {
        left: 20px;
      }
      .slick-next {
        right: 10px;
      }
    }
    @include response(900px) {
      max-width: 100%;
    }
  }
  @include response(900px) {
    padding: 60px 0;
  }
}