.footer {
  padding: 60px 0;
  background: rgb(238,245,255);
  background: linear-gradient(90deg, rgba(238,245,255,1) 0%, rgba(238,245,255,1) 50%, rgba(208,228,242,1) 50%, rgba(208,228,242,1) 100%);
  .container {
    @include response(1070px) {
      padding: 0;
    }
  }
  &-inner {
    display: flex;
    justify-content: space-between;
    @include response(1070px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &-left {
    width: 100%;
    max-width: 480px;
    &__inner {
      max-width: 480px;
      width: 100%;
      display: flex;
      justify-content: space-between;
      @include response(1070px) {
        margin: 0 auto;
      }
      @include response(767px) {
        flex-wrap: wrap;
      }
    }
    @include response(1070px) {
      max-width: 100%;
      background: #eef5ff;
      padding: 40px 15px;
    }
  }
  &-right {
    max-width: 500px;
    width: 100%;
    &__inner {
      max-width: 500px;
      width: 100%;
      @include response(1070px) {
        margin: 0 auto;
      }
    }
    @include response(1070px) {
      max-width: 100%;
      background: #D0E4F2;
      padding: 40px 15px;
    }
  }
  &-nav {
    max-width: 237px;
    &__title {
      font-weight: 500;
      font-size: 20px;
      line-height: 155%;
      color: #141431;
      padding-bottom: 15px;
      margin-bottom: 15px;
      border-bottom: 2px solid rgba(#141431, .2);
    }
    &-list {
      padding: 0;
      margin: 0;
      list-style: none;
      li {
        margin-bottom: 20px;
      }
      a {
        line-height: 145%;
        color: #141431;
        text-decoration: none;
        display: inline-block;
        @include transition();
        &:hover {
          color: #CC7F32;
        }
      }
    }
    @include response(767px) {
      max-width: 100%;
      margin-bottom: 40px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__logo {
    margin-bottom: 30px;
  }
  &__descr {
    line-height: 145%;
    margin-bottom: 30px;
  }
  &__label {
    position: relative;
    font-weight: 600;
    font-size: 22px;
    line-height: 125%;
    color: #141431;
    display: inline-block;
    margin-bottom: 25px;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -2px;
      width: 100%;
      height: 2px;
      background: linear-gradient(270deg, #CC7F32 0%, #FFCC8F 100.24%);
    }
  }
  .btn {
    height: 56px;
    margin-bottom: 30px;
  }
  @include response(1070px) {
    padding: 0;
    background: #fff;
  }
}

.contacts-list {
  margin-bottom: 40px;
  &__item {
    display: inline-flex;
    align-items: center;
    text-decoration: none;
    gap: 10px;
    margin-bottom: 20px;
    line-height: 145%;
    svg {
      flex-shrink: 0;
    }
    &-inner {
      color: #141431;
      font-weight: 500;

      span {
        font-weight: 400;
      }
    }
  }
}