.contact {
  &-wrapp {
    background: #D0E4F2;
    padding: 100px 0;
    margin-bottom: 100px;
    @include response(900px) {
      padding: 60px 0;
      margin-bottom: 60px;
    }
  }
}
.map {
  &-wrapp {
    margin-bottom: 100px;
    @include response(900px) {
      margin-bottom: 60px;
    }
  }
  &-title {
    font-weight: 600;
    font-size: 48px;
    line-height: 125%;
    color: #141431;
    margin-bottom: 30px;
    @include response(900px) {
      font-size: 32px;
    }
  }
  &-heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 15px;
    margin-bottom: 20px;
    .btn {
      height: 49px;
      max-width: 210px;
      font-size: 16px;
    }
    @include response(900px) {
      display: block;
    }
  }
  &-label {
    display: flex;
    align-items: center;
    gap: 15px;
    font-weight: 600;
    font-size: 22px;
    line-height: 125%;
    color: #141431;
    span {
      color: #CC7F32;
    }
    @include response(900px) {
      font-size: 18px;
      margin-bottom: 30px;
    }
  }
  &-inner {
    iframe {
      border: none;
      width: 100%;
      max-width: 100%;
    }
  }
}

.form-confirm {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 30px;
  text-align: center;
  padding: 50px 80px;
  &__title {
    font-weight: 600;
    font-size: 38px;
    line-height: 125%;
    color: #141431;
  }
  &__caption {
    line-height: 145%;
  }
  @include response(900px) {
    padding: 40px 15px;
  }
}