.about-page {
  .team {
    padding-bottom: 40px;
    @include response(1000px) {
      padding-bottom: 20px;
    }
  }
}

.about-heading {
  background-image: url("../data/hero.jpg");
  background-size: cover;
  background-position: center center;
  padding: 100px 0;
  margin-bottom: 100px;
  &__holder {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 40px;
    @include response(900px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__inner {
    max-width: 580px;
  }
  &__thumb {
    flex-shrink: 0;
    max-width: 50%;
    filter: drop-shadow(4px 15px 60px rgba(0, 0, 50, 0.2));
    border-radius: 5px;
    overflow: hidden;
    @include response(900px) {
      max-width: 100%;
      width: 680px;
    }
  }
  &__label {
    height: 51px;
    background: #FFFFFF;
    border-radius: 4px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    padding: 0 15px;
    color: #141431;
    font-weight: 600;
    font-size: 22px;
    line-height: 125%;
    margin-bottom: 10px;
    &::after {
      content: '';
      position: absolute;
      left: 15px;
      right: 15px;
      height: 1px;
      bottom: 10px;
      background: linear-gradient(270deg, #CC7F32 0%, #FFCC8F 100.24%);
    }
  }
  &__title {
    font-weight: 600;
    font-size: 72px;
    line-height: 125%;
    color: #141431;
    margin-bottom: 30px;
    span {
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-image: linear-gradient(90deg, #CC7F32 -0.48%, #FFCC8F 99.76%);
    }
    @include response(1200px) {
      font-size: 48px;
    }
    @include response(900px) {
      font-size: 38px;
      span {
        display: block;
      }
    }
  }
  &__caption {
    margin-bottom: 30px;
  }
  @include response(900px) {
    padding: 40px 0;
    margin-bottom: 40px;
  }
}

.about-block {
  margin-bottom: 100px;
  &__holder {
    max-width: 1210px;
    padding: 0 15px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 40px;
    &.reverse {
      flex-direction: row-reverse;
      .about-block__thumb::after {
        background-image: url("../data/about-block-bg-2.svg");
      }
    }
    @include response(900px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__thumb {
    flex-shrink: 0;
    max-width: 610px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    &::after {
      content: '';
      width: 100%;
      height: 110px;
      background-image: url("../data/about-block-bg.svg");
      background-size: cover;
      background-position: center;
    }
    &-list {
      display: flex;
      gap: 5px;
    }
    &-column {
      display: flex;
      flex-direction: column;
      gap: 5px;
      .about-block__thumb-item {
        border-radius: 5px;
        overflow: hidden;
      }
    }
    &.with-slider {
      &::after {
        display: none;
      }
    }
    @include response(1100px) {
      width: 50%;
    }
    @include response(900px) {
      width: 100%;
    }
  }
  &__inner {
    max-width: 480px;
  }
  &__title {
    font-weight: 600;
    font-size: 48px;
    line-height: 125%;
    color: #141431;
    margin-bottom: 30px;
    span {
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-image: linear-gradient(90deg, #CC7F32 -0.48%, #FFCC8F 99.76%);
    }
    @include response(1100px) {
      font-size: 38px;
    }
    @include response(900px) {
      font-size: 32px;
      margin-bottom: 0;
    }
  }
  &__descr {
    margin-bottom: 30px;
  }
  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    gap: 20px;
    &-thumb {
      flex-shrink: 0;
    }
    &-caption {
      line-height: 125%;
      color: #141431;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__slider {
    margin-bottom: 48px !important;
    .slick-arrow {
      z-index: 2;
      height: 64px;
      width: 34px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: #306185;
      border-radius: 4px;
      &::before {
        content: '';
        width: 22px;
        height: 22px;
        opacity: 1;
        background-image: url("../data/arrow.svg");
        background-size: contain;
      }
    }
    .slick-prev {
      left: 5px;
    }
    .slick-next {
      right: 5px;
      &::before {
        transform: scale(-1, 1);
      }
    }
    .slick-dots {
      display: flex;
      justify-content: center;
      gap: 5px;
      li {
        width: 12px;
        height: 8px;
        background: #D0E4F2;
        border-radius: 4px;
        @include transition(.5s);
        &.slick-active {
          background: #306185;
          width: 24px;
        }
        button {
          display: none;
        }
      }
    }
    &-nav {
      &-item {
        border: 1px solid #D0E4F2;
        border-radius: 4px;
        padding: 10px 30px;
        text-align: center;
        font-size: 14px;
        line-height: 16px;
        color: #141431;
        &__label {
          margin-bottom: 10px;
        }
        &__title {
          font-size: 18px;
          font-weight: 600;
          color: #306185;
        }
      }
    }
  }
  @include response(900px) {
    margin-bottom: 60px;
  }
}

.services {
  padding: 100px 0;
  background: #EEF5FF;
  margin-bottom: 100px;
  &-list {
    display: flex;
    gap: 10px;
    @include response(800px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &-item {
    background: #FFFFFF;
    border: 1px solid #D0E4F2;
    padding: 60px 50px;
    width: 454px;
    max-width: calc(33.33333% - 10px);
    position: relative;
    &__icon {
      text-align: center;
      margin-bottom: 30px;
    }
    &__title {
      margin-bottom: 30px;
      font-weight: 600;
      font-size: 26px;
      line-height: 125%;
      text-align: center;
      color: #141431;
    }
    &::before {
      content: '';
      width: 75px;
      height: 75px;
      background-image: url("../data/corner.svg");
      position: absolute;
      left: 0;
      top: 0;
    }
    @include response(1000px) {
      padding: 30px 15px;
    }
    @include response(800px) {
      width: 100%;
      max-width: 335px;
    }
  }
  @include response(900px) {
    padding: 40px 0;
    margin-bottom: 60px;
  }
}