.error {
  &-page {
    background: #D0E4F2;
    .header-bot__inner {
      justify-content: space-between;
    }
    .breadcrumbs {
      margin: 0;
    }
  }
  &-inner {
    max-width: 810px;
    padding: 0 15px;
    margin: 50px auto 100px;
    text-align: center;
    &__thumb {
      margin-bottom: 60px;
      @include response(900px) {
        margin-bottom: 40px;
      }
    }
    &__title {
      font-weight: 600;
      font-size: 38px;
      line-height: 125%;
      color: #141431;
      margin-bottom: 30px;
      span {
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-image: linear-gradient(90deg, #CC7F32 -0.48%, #FFCC8F 99.76%);
      }
      @include response(900px) {
        font-size: 32px;
      }
    }
    &__caption {
      line-height: 145%;
      margin-bottom: 60px;
      @include response(900px) {
        margin-bottom: 40px;
      }
    }
    .btn {
      margin: 0 auto;
    }
    @include response(900px) {
      margin: 40px auto;
    }
  }
}