.faq {
  &-wrapp {
    background: #D0E4F2;
    padding: 100px 0;
    @include response(900px) {
      padding: 60px 0 30px;
    }
  }
  &__title {
    font-weight: 600;
    font-size: 72px;
    line-height: 125%;
    color: #141431;
    margin-bottom: 30px;
    span {
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-image: linear-gradient(90deg, #CC7F32 -0.48%, #FFCC8F 99.76%);
    }
    @include response(900px) {
      font-size: 32px;
    }
  }
  &-inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    .appointment-inner {
      max-width: 100%;
      width: 100%;
      padding: 50px;
      @include response(900px) {
        padding: 30px 0;
      }
    }
    .appointment__contacts {
      margin-bottom: 30px;
    }
    .btn {
      height: 56px;
      max-width: 321px;
    }
    @include response(900px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &-column {
    max-width: calc(50% - 15px);
    width: 100%;
    @include response(900px) {
      max-width: 600px;
    }
  }
  &-block {
    background: #FFFFFF;
    border: 1px solid #306185;
    box-shadow: 4px 4px 10px rgba(0, 0, 50, 0.1);
    border-radius: 4px;
    padding: 30px;
    margin-bottom: 30px;
    &__title {
      font-weight: 600;
      font-size: 38px;
      line-height: 125%;
      color: #141431;
      margin-bottom: 10px;
      @include response(900px) {
        font-size: 32px;
      }
    }
    @include response(1100px) {
      padding: 30px 15px;
    }
  }
  &-item {
    padding: 20px 20px 0;
    @include transition();
    &__heading {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-bottom: 10px;
      border-bottom: 2px solid rgba(48, 97, 133, 0.2);
      margin-bottom: 20px;
      cursor: pointer;
      @include transition();
    }
    &__title {
      color: #141431;
      display: flex;
      align-items: center;
      gap: 10px;
      line-height: 145%;
    }
    &__opener {
      @include transition();
    }
    &.active {
      background: #EEF5FF;
      border-bottom: 2px solid #306185;
      border-radius: 4px 4px 0px 0px;
      padding-bottom: 20px;
      margin-bottom: 0;
      .faq-item__heading {
        border-color: transparent;
      }
      .faq-item__opener {
        transform: rotate(180deg);
      }
    }
    @include response(1100px) {
      padding: 10px;
    }
  }
}

.accordion__slide {
  display: none;
}