.blog-page {
  .blog-block {
    margin-top: -150px;
    margin-bottom: 60px;
    @include response(900px) {
      margin-bottom: 40px;
    }
  }
  .blog-list {
    margin-bottom: 80px;
    @include response(900px) {
      margin-bottom: 40px;
    }
  }
}

.blog-heading {
  background: #306185;
  padding: 75px 15px 225px;
  &__inner {
    max-width: 780px;
    margin: 0 auto;
    text-align: center;
    color: #fff;
  }
  &__title {
    font-weight: 600;
    font-size: 48px;
    line-height: 125%;
    margin-bottom: 30px;
    span {
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-image: linear-gradient(90deg, #CC7F32 -0.48%, #FFCC8F 99.76%);
    }
    @include response(900px) {
      font-size: 32px;
    }
  }
  &__caption {
    line-height: 145%;
  }
  @include response(900px) {
    padding: 40px 15px 190px;
  }
}