body {
  height: 100%;
  font-family: 'Campton', sans-serif;
  font-size: 18px;
  color: #5D5D70;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: none;
}
* {
  outline: none;
  box-sizing: border-box;
  &::after,
  &::before {
    box-sizing: border-box;
  }
}
svg {
  transform: translateZ(0);
}
input,
textarea,
button {
  font-family: 'Campton', sans-serif;
  font-size: 18px;
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
input[type="submit"] {
  cursor: pointer;
}
h1 {
  font-weight: 600;
  font-size: 48px;
  line-height: 125%;
  margin: 0 0 30px;
}
h2 {
  font-weight: 600;
  font-size: 38px;
  line-height: 125%;
  margin: 0 0 20px;
}
h3 {
  font-weight: 600;
  font-size: 32px;
  line-height: 125%;
  margin: 0 0 20px;
}
h4 {
  font-weight: 600;
  font-size: 26px;
  line-height: 125%;
  margin: 0 0 20px;
}
h5 {
  font-weight: 600;
  font-size: 22px;
  line-height: 125%;
  margin: 0 0 20px;
}
h6 {
  font-weight: 600;
  font-size: 18px;
  line-height: 125%;
  margin: 0 0 20px;
}
p.error {
  font-size: 12px;
  line-height: 14px;
  color: red;
}
.wrapper {
  overflow: hidden;
  min-height: 100vh;
  position: relative;
}
.holder-block {
  overflow: hidden;
  position: relative;
  height: 100%;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #306185;
  border-radius: 4px;
  width: 329px;
  max-width: 100%;
  height: 81px;
  padding: 0 15px;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
  border: none;
  gap: 10px;
  @include transition();
  svg {
    margin-top: -2px;
    path {
      @include transition();
    }
  }
  span {
    position: relative;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -1px;
      width: 100%;
      height: 1px;
      background: linear-gradient(270deg, #CC7F32 0%, #FFCC8F 100.24%);
    }
  }
  &:hover {
    background: #FFCC8F;
    color: #141431;
    svg path {
      fill: #141431;
    }
  }
}

.content-area {
  line-height: 145%;
  a {
    color: #CC7F32;
    text-decoration: none;
    font-weight: 500;
  }
  p {
    margin: 0 0 30px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  ul {
    margin: 0 0 30px;
    list-style: none;
    padding: 0;
    li {
      position: relative;
      padding-left: 26px;
      line-height: 145%;
      margin-bottom: 10px;
      &::before {
        position: absolute;
        content: '';
        width: 16px;
        height: 16px;
        background-image: url("../data/pointer.svg");
        background-size: contain;
        left: 0;
        top: 3px;
      }
    }
  }
}
.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1210px;
  &--big {
    max-width: 1410px;
  }
}

.form {
  background: #fff;
  padding: 80px;
  border: 1px solid #306185;
  box-shadow: 4px 4px 10px rgba(0, 0, 50, 0.1);
  border-radius: 4px;
  width: 100%;
  line-height: 145%;
  &__title {
    font-weight: 600;
    font-size: 26px;
    line-height: 125%;
    text-align: center;
    color: #141431;
    margin-bottom: 30px;
  }
  &-item {
    position: relative;
    margin-bottom: 20px;
    &__field {
      border-bottom: 2px solid #D0E4F2;
      display: flex;
      align-items: center;
      gap: 10px;
      svg {
        flex-shrink: 0;
      }
    }
  }
  &-field {
    border: none;
    padding: 10px 0;
    width: 100%;
    color: #141431;
    &::placeholder {
      color: rgba(#5D5D70, .4);
    }
  }
  &-area {
    border: none;
    border-bottom: 2px solid #D0E4F2;
    color: #141431;
    width: 100%;
    height: 200px;
    resize: none;
    &::placeholder {
      color: rgba(#5D5D70, .4);
    }
  }
  &-btn {
    margin: 0 auto;
  }
  @include response(900px) {
    padding: 40px 20px;
  }
}

.gender-select {
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
  padding-bottom: 10px;
  border-bottom: 2px solid #5D5D70;
  &__title {
    color: #141431;
  }
  &__option {
    cursor: pointer;
    position: relative;
    height: 26px;
    @include response(900px) {
      height: 52px;
    }
  }
  &__box {
    height: 26px;
    background: #EEF5FF;
    border-radius: 4px;
    display: inline-flex;
    align-items: center;
    padding: 0 15px;
    color: #141431;
    font-size: 15px;
    font-weight: 500;
    border: 1px solid transparent;
    @include transition();
    @include response(900px) {
      padding: 0 5px;
      height: 52px;
    }
  }
  &__options {
    display: flex;
    gap: 10px;
    input {
      height: 0;
      width: 0;
      opacity: 0;
      position: absolute;
      &:checked + .gender-select__box {
        border-color: #306185;
        background: #D0E4F2;
      }
    }
  }
}

.custom-checkbox {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  cursor: pointer;
  margin: 30px 0;
  &__box {
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #5D5D70;
    border-radius: 4px;
    flex-shrink: 0;
    svg {
      @include transition();
      opacity: 0;
    }
  }
  &__label {
    font-size: 14px;
    line-height: 16px;
    color: #141431;
    a {
      color: #306185;
      font-weight: 500;
    }
  }
  input {
    width: 0;
    height: 0;
    opacity: 0;
    &:checked + .custom-checkbox__box svg {
      opacity: 1;
    }
  }
}