.breadcrumbs {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin: 0 0 40px;
  li {
    display: flex;
    align-items: center;
    &::after {
      content: '/';
      font-weight: 500;
      font-size: 14px;
      color: #FFCC8F;
      margin: 0 15px;
    }
    &:last-child::after {
      display: none;
    }
  }
  &__item {
    color: #fff;
    text-decoration: none;
    font-weight: 500;
    font-size: 14px;
  }
}