.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin: 0;
  gap: 5px;
  li {
    font-weight: 500;
    font-size: 20px;
    color: #141431;
  }
  &__item {
    width: 48px;
    height: 48px;
    text-decoration: none;
    border-radius: 4px;
    color: #141431;
    font-weight: 500;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    @include transition();
    &.active,
    &:hover {
      background: #D0E4F2;
    }
  }
}