@mixin font($fontname, $filename, $fontweight, $fontstyle) {
  @font-face {
    font-family: $fontname;
    src: url(#{'../fonts/'+$filename+'.eot'});
    src:
      local('ðŸ˜¥'),
      url(#{'../fonts/'+$filename+'.woff'}) format("woff"),
      url(#{'../fonts/'+$filename+'.ttf'}) format("truetype");
    font-weight: $fontweight;
    font-style: $fontstyle;
  }
}

@include font('Campton', 'CamptonBook', 400, normal);
@include font('Campton', 'CamptonMedium', 500, normal);
@include font('Campton', 'CamptonSemiBold', 600, normal);
@include font('Campton', 'CamptonBold', 700, normal);
@include font('Campton', 'CamptonBlack', 800, normal);
@include font('Campton', 'CamptonThin', 100, normal);
