.article {
  &-heading {
    padding: 20px 15px 180px;
    background: #306185;
    &__info {
      display: flex;
      align-items: center;
      justify-content: center;
      &-item {
        padding: 0 15px;
        display: flex;
        align-items: center;
        gap: 5px;
        font-size: 14px;
        line-height: 16px;
        color: #fff;
        border-right: 2px solid rgba(#D0E4F2, .4);
        span {
          font-weight: 600;
        }
        &:last-child {
          border: none;
        }
      }
    }
  }
  &-inner {
    max-width: 810px;
    padding: 0 15px;
    margin: -150px auto 60px;
    &__thumb {
      filter: drop-shadow(4px 15px 80px rgba(0, 0, 50, 0.15));
      border-radius: 4px 4px 100px 4px;
      overflow: hidden;
      margin-bottom: 60px;
    }
  }
  &-content {
    color: #141431;
  }
  &-info {
    background: #EEF5FF;
    border: 1px solid #D0E4F2;
    box-shadow: 4px 4px 10px rgba(0, 0, 50, 0.1);
    border-radius: 4px;
    padding: 40px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    img {
      display: inline-block;
      margin-bottom: 20px;
    }
    &__label {
      font-weight: 600;
      font-size: 32px;
      line-height: 125%;
      color: #141431;
      margin-bottom: 30px;
      position: relative;
      &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -2px;
        width: 100%;
        height: 2px;
        background: -webkit-gradient(linear, right top, left top, from(#CC7F32), color-stop(100.24%, #FFCC8F));
        background: linear-gradient(270deg, #CC7F32 0%, #FFCC8F 100.24%);
      }
    }
    .btn {
      max-width: 240px;
      @include response(900px) {
        order: 2;
      }
    }
    .btn,
    .appointment__contacts-item {
      height: 56px;
      color: #fff;
      &:hover {
        color: #141431;
      }
    }
    @include response(900px) {
      padding: 20px;
      .appointment__contacts {
        gap: 10px;
        &-item {
          padding: 0 15px;
        }
      }
    }
  }
}