.header {
  padding: 30px 15px;
  background: #EEF5FF;
  &__inner {
    max-width: 1380px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn {
    color: #fff;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    padding: 0 15px;
    background: #141431;
    box-shadow: 4px 4px 10px rgba(0, 0, 50, 0.1);
    border-radius: 4px;
    width: 194px;
    max-width: 100%;
    height: 49px;
    span {
      position: relative;
      &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -1px;
        width: 100%;
        height: 1px;
        background: linear-gradient(270deg, #CC7F32 0%, #FFCC8F 100.24%);
      }
    }
    @include response(1050px) {
      display: none;
    }
  }
  &-bot {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    background: #141431;
    &__inner {
      display: flex;
      max-width: 1410px;
      padding: 0 15px;
      width: 100%;
      align-items: center;
      justify-content: flex-end;
    }
    &-contacts {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      gap: 15px;
      font-size: 14px;
      color: rgba(#fff, .6);
      &__item {
        color: #fff;
        display: flex;
        align-items: center;
        font-weight: 500;
        text-decoration: none;
        gap: 5px;
      }
    }
  }
  @include response(900px) {
    padding: 15px;
  }
}
.navigation {
  &-list {
    display: flex;
    align-items: center;
    gap: 60px;
    list-style: none;
    padding: 0;
    margin: 0;
    & > li {
      position: relative;
      &.has-child {
        &:hover {
          .submenu {
            display: block;
          }
        }
      }
    }
    & > li > a {
      display: inline-flex;
      align-items: center;
      font-weight: 500;
      font-size: 18px;
      color: #141431;
      text-decoration: none;
    }
    @include response(1200px) {
      gap: 30px;
    }
  }
  @include response(1050px) {
    display: none;
  }
}

.burger {
  display: none;
  @include response(1050px) {
    display: flex;
  }
}

.submenu {
  position: absolute;
  width: 490px;
  background: #fff;
  left: 0;
  top: 100%;
  padding: 10px 0;
  margin: 0;
  list-style: none;
  box-shadow: 5px 5px 15px rgba(0, 0, 50, 0.2);
  border-radius: 4px;
  z-index: 5;
  display: none;
  &-item {
    display: flex;
    align-items: center;
    position: relative;
    padding: 10px 30px 10px 20px;
    gap: 15px;
    cursor: pointer;
    @include transition();
    a {
      font-weight: 500;
      color: #141431;
      text-decoration: none;
      @include transition();
      &:hover {
        color: #CC7F32;
      }
    }
    &__icon {
      width: 42px;
      height: 42px;
      background: #EEF5FF;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__arrow {
      position: absolute;
      right: 20px;
      top: calc(50% - 5px);
    }
    &:hover {
      background: #EEF5FF;
      .third-menu {
        display: block;
      }
    }
  }
}
.third-menu {
  position: absolute;
  left: 100%;
  top: -20px;
  background: #FFFFFF;
  box-shadow: 5px 5px 15px rgba(0, 0, 50, 0.2);
  border-radius: 4px;
  padding: 10px 0;
  margin: 0;
  list-style: none;
  width: 265px;
  display: none;
  a {
    padding: 20px;
    border-bottom: 1px solid #D0E4F2;
    display: block;
  }
  li:last-child a {
    border: none;
  }
}